.select
{
    font-family: 'Open Sans', sans-serif;
    font-size: 1em;

    flex: 1;

    padding: 0 .5em;

    cursor: pointer;

    color: #fff;
    border: 0!important;
    outline: 0;
    background: #5c6664;
    background-image: none;
    box-shadow: none;

    -webkit-appearance: none;
       -moz-appearance: none;
        -ms-appearance: none;
            appearance: none;
}
select::-ms-expand
{
    display: none;
}
.select
{
    line-height: 3;

    position: relative;

    display: flex;
    overflow: hidden;

    width: 20em;
    height: 3em;

    border-radius: .25em;
    background: #5c6664;
}
.select::after
{
    position: absolute;
    top: 0;
    right: 0;

    padding: 0 1em;

    content: '\25BC';
    cursor: pointer;
    transition: .25s all ease;
    pointer-events: none;

    background: #2b2e2e;
}
.select:hover::after
{
    color: #23b499;
}